import React, { FC, useEffect, useState } from 'react';
import { Link } from '../Common/Link';
import { Text } from '../Common/Typography';
import styles from './Contact.module.css'
interface Props {

}
export const Contact: FC<Props> = () => {
  const [href, setHref] = useState('')
  const grecaptcha = window['grecaptcha' as any] as any;
  useEffect(() => {
    grecaptcha.ready(async () => {
      try {
        const responseToken = await grecaptcha.execute('6LePh7UUAAAAAATOtJW_S-WM_VqBa3eU1ZwP-Bv5', { action: 'homepage' });
        const requestInit: RequestInit = { method: "POST", body: JSON.stringify({ responseToken }), };
        const resp = await fetch('/', requestInit);
        if (resp && resp.ok) {
          const data = await resp.json()
          if (data) {
            setHref(`mailto:${data.email}?subject=${atob('SW50ZXJlc3QgZnJvbSBQb3J0Zm9saW8gcGFnZQ')}`)
          }
        }
      } catch (err) {
        console.error(err)
      }

    })
  }, [grecaptcha])
  return href ? (
    <div
      className={styles.contact}
    >
      <Link
        href={href}
      >
        <Text center size={16}>Contact Me</Text>
      </Link>
    </div>) : <div></div>
}